//loading effect

.circle-spin{
  width: 80px;
  height:80px;
  position: relative;
  margin: 64px auto;
  display: none;
  >div{
    width: 100%;
    height: 100%;
    border: 8px solid #282E4D;
    border-radius: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-color:  #282E4D transparent transparent transparent ;
    animation: turnaround 1.2s ease-in-out infinite;
    &:nth-child(1){ 
      animation-delay: -0.45s;
    }
    &:nth-child(2){      
      animation-delay: -0.3s;
    }
    &:nth-child(3){
      animation-delay: -0.15s;
    }    
  }
}
.loading{
  *{
    display: none!important;
  }
  .circle-spin{
    display: block!important;
    >div{
      display: block!important;
    }
  }
}

@keyframes turnaround{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}


