          // color: $color-secondary2;
          color: $btn-default-hover-color;
          padding-left: 25px;
          
        }
        &:hover:before, &.active:before, &.active:hover:before{
          transform: translateX(0);
          
        }
      }
      &.item-disable > a:hover{
        padding-left: 10px;
        @include rwd(laptop-s){
          padding-left: 8px;
        }
        &:before{
          display: none;
        }
