.v2-cate-nav{
  width: 25%;
  float: left;
  padding: 14px 20px;
  border-right: 1px #fff solid;
  @include rwd(tablet-xl){
    width: 30%;
  }
  @include rwd(tablet-l){
    width: 35%;
  }
  @include rwd(mobile-xl){
    width: auto;
    float: none;
    padding-left: 16px;
    padding-right: 16px;;
    position: relative;
  }
  .cate-nav-toggle{
    display: block;
    background: #666893;
    color: #fff;
    padding: 4px 12px;
    padding-right: 27px;
    position: relative;
    cursor: pointer;
    border:1px transparent solid;
    overflow : hidden;
    text-overflow : ellipsis;
    white-space : nowrap;
    &.cate-nav-toggle-active{
      background: #fff;
      color: $font-color-dark2;
      border-color: $font-color-light1;
    }
    > i:first-child{
      padding-right: 5px;
    }
    > i:last-child{
      position: absolute;
      top: 50%; 
      right: 12px;
      transform: translateY(-50%);
    }
  }
  /*To Do: 增加控制的css**/
  .cate-nav-toggle-active{
    box-shadow: 0px 0px 1920px 1920px rgba(0,0,0,0.8);
    z-index: 99;
  }
  .cate-nav-toggle-active+.cate-nav-dropdown{
      display: block!important;
  }
  .cate-nav-active{
      overflow: hidden;
  }
  .cate-nav-dropdown{
      max-height: calc(90vh - 80px);
  }
  /*To Do: 增加控制的css_END**/
  .cate-nav-dropdown{
    display: none;
    background: #fff;
    padding: 12px 20px;    
    text-align: left;
    position: absolute;
    top: 100%;
    left: 20px;
    right: 20px;
    transform: translateY(-15px);
    border: 1px $font-color-light1 solid;
    z-index: 1000;
    @include rwd(mobile-xl){
      left: 16px;
      right: 16px;
    }
    &.cate-nav-dropdown-active{
      display: block;
      
      
    }
    .simplemenu-normal-icon{
      display: none;
    }
    ul{
      @include clearul;
    }
    li > a:after{
      display: inline-block;
        font: normal normal normal 14px/1 FontAwesome;
        font-size: inherit;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        position: absolute;
        right: 15px;
        top: 8px;
        content: "\f105";
        display: none;        
        //color: $active-color;
    }
    li> a:hover:after{
      display: block;
    }

    li.has-submenu{
      >a:after{        
        content: "\f107";      
        display: block;  
      }   
      &.active > a:after{
        transform: rotate(180deg);
      }   
    }
    > ul > li{
      border: 0;      
      &.active > a{
        background: $active-color;
        color: #fff;
        &:hover{
          background: $active-color;
          color: #fff;
        }
      }
    }
    > ul > li + li{
      margin-top: 8px;
    }
    > ul > li > a{
      color: $font-color-dark2;
      font-size: 16px;
      padding:2px 20px;
      border-radius: 20px;
      border: 1px transparent solid;
      //background: $font-color-light3;
      &.allcate{
        border-color: $font-color-light2;
        background: #fff;
      }
      &:hover{
        background: $font-color-light2;
      }
      &.active{
        background: $font-color-dark1;
        color: #fff;
      }
    }
    > ul > li > ul{
      border: 0;
      padding: 5px 0 5px 20px;
      border-bottom:1px $font-color-light2 dashed;
    }
    > ul > li > ul > li{
      &.active > a{
        background: $active-color;
        color: #fff;
        &:hover{
          background: $active-color;
          color: #fff;
        }
      }
    }
    ul > li > ul > li + li{
      margin-top: 4px;
    }
    > ul > li > ul > li > a{
      font-size: 16px;
      padding: 2px 20px;
      border-radius: 20px;
      color: $font-color-dark2;
      position: relative;
      &.allcate{
        color: $active-color;
        font-weight: 500;
        text-decoration: underline;
      }
      
      &:before{
        content: '';
        width: 2px;
        height: 2px;
        background: $font-color-dark2;
        border-radius: 50%;
        position: absolute;
        top: 15px;
        left: 10px;
      }
      &:hover{
        background: $font-color-light2;
      }
      &.active{
        background: $font-color-dark1;
        color: #fff;
      }
    }
