//相關產品及相關型錄的細部樣式
.related-product-lists-wrap,
.related-catalog-lists-wrap,
.related-exhi-article-lists-wrap{  
  .slick-slider{
    padding-bottom: 50px;
    margin: 0 -15px;
    @include rwd(tablet-l){
      padding-bottom: 45px;
      margin: 0 -10px;
    }
    @include rwd(mobile-l){
      padding-bottom: 40px;
    }
  }
  .slick-prev,
  .slick-next{
    top: auto;
    bottom: 0;
    border: 1px $font-color-light1 solid;
    width: 32px;
    height: 32px;
    transform: none;   
    @include rwd(mobile-l){
      width: 28px;
      height: 28px;
    }
    
    &:hover{
      background: $hover-color;
      color: #fff;
      &:before{
        color: #fff;
      }
    }
    &:before{
      display: inline-block;
      font: normal normal normal 14px/1 FontAwesome;
      font-size: inherit;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;      
      font-size: 20px;
      color: $font-color-dark2;
    }
  }
  .slick-prev{    
    left: auto;
    right: 50%;
    margin-right: 3px;
    &:before{
      content: "\f104";
    }
  }
  .slick-next{
    right: auto;
    left: 50%;
    margin-left: 3px;
    &:before{
      content: "\f105";
    }
  }
  .slick-slide{
    padding: 0 15px;
    @include rwd(tablet-l){
      padding: 0 10px;
    }
    .img-wrap{
      border:1px $font-color-light2 solid;
      display: block;
      position: relative;
      > a:first-child{
        display: block;
        height: 0;
        padding-bottom: 75%;
        overflow: hidden;
        position: relative;
        &:before{
          content: '';
          background: $font-color-dark2;
          position: absolute;
          opacity: 0;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          z-index: 2;
          @include transition;
        }          
        img{
          position: absolute;
          max-width: 96%;
          max-height: 96%;
          top: 50%;
          left: 50%;
          transform: translateY(-50%) translateX(-50%);
        }               
      }      
    }
    .product-title,
    .catalog-name{
      position: absolute;
      top: 50%;
      left: 10px;
      right: 10px;
      transform: translateY(-50%);
      z-index: 3;
      color: #fff;
      font-size: 18px;
      font-weight: 500;
      opacity: 0;
    }
    &:hover{
      .img-wrap a:before{
        opacity: 0.8;
      }
      .product-title,
      .catalog-name{
        opacity: 1;
      }
    }
  }
}
.related-exhi-article-lists-wrap{
  .slick-slide .img-wrap > a:first-child img{
    padding: 0;
    max-width: 100%;
    max-height: 100%;
  }
  .article-item{
    text-align: left;
    position: relative;
    margin-bottom: 15px;
    .img-wrap{
      a{
        position: relative;
        overflow: hidden;
        height: 0;
        padding-bottom: 4/3*100%;
      }      
      > a:first-child img{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        &.img_W{
          height: 100%;
          max-height: 100%;
          max-width: none;
        }
        &.img_H{
            width: 100%;
            max-width: 100%;
            max-height: none;
        }
      }
    } 
  }
  .article-item_date{
    color: $font-color-dark5;
    font-size: 20px;
    @include rwd(laptop){
      font-size: 18px;
    }
    @include rwd(tablet-xl){
      font-size: 17px;
    }
  }
  .article-item_title{
    a{
      display: block;
      color: $header-color;
      font-size: 20px;
      line-height: 1.3;
      @include rwd(laptop){
        font-size: 18px;
      }
      @include rwd(tablet-xl){
        font-size: 17px;
      }
    }
  }
  .article-item_fulllink a{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: '';
  }
  .slick-slide:hover{
    .article-item_title a{
      color: $hover-color2;
    }
  }
