//相關影片的樣式
.product-video-lists-wrap,
.related-video-lists-wrap{
  .related-video_lists{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    margin: 0 -15px;
  }
  .related-video_item{
    width: 50%;
    padding: 0 15px;
    margin: 10px auto;
    @include rwd(tablet-s){
      width: 100%;
    }
    .img-wrap{
      border: 1px #E5E6EA solid;
    }
    a{
      display: block;
      position: relative;
      overflow: hidden;
      height: 0;
      padding-bottom: 9/16*100%;
      
      &:before{
        background: $font-color-dark2;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        content: '';
         opacity: 0;
        @include transition;
        z-index: 1;
      }
      
      img{
        @include transition;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        z-index: 0;
        &.img_W{
          height: 100%;
          max-height: 100%;
          max-width: none;
        }
        &.img_H{
            width: 100%;
            max-width: 100%;
            max-height: none;
        }
      }
      .related-video__title{
        position: absolute;
        top: 50%;
        left: 10%;
        right: 10%;
        color: #fff;
        z-index: 2;
        font-size: 20px;
        line-height: 1.5;
        opacity: 0;
        @include transition;
        transform: translateY(-50%);
        @include rwd(tablet-xl){
          font-size: 19px;
        }
        @include rwd(mobile-xl){
          font-size: 18px;
        }
        @include rwd(mobile-l){
          font-size: 16px;
        }
        &:after{
          display: block;
          font: normal normal normal 14px/1 FontAwesome;
          font-size: inherit;
          text-rendering: auto;
          -webkit-font-smoothing: antialiased;
          -moz-osx-font-smoothing: grayscale;
          content: "\f144";
          color: #fff;
          font-size: 120px;
          // position: absolute;
          // top: 40%;
          // left: 50%;
          // transform: translateX(-50%);
          // opacity: 0;
          // @include transition;
          // z-index: 1;
          @include rwd(laptop-l){
            font-size: 120px * .9;
          }
          @include rwd(laptop-s){
            font-size: 120px * .8;
          }
          @include rwd(tablet-xl){
            font-size: 120px * .7;
          }
          @include rwd(mobile-xl){
            font-size: 120px * .6;
          }
          @include rwd(mobile-l){
            font-size: 120px * .5;
          }
        }
      }
      &:hover{
        &:before{
          opacity: .8;
          
        }
        &:after{
          opacity: 1;
        }
        img{
          transform: translateX(-50%) translateY(-50%) scale(1.1);
        }
        .related-video__title{
          opacity: 1;
        }
      }
    }
  }
}
