.exhi-vrshowroom-wrap{
  .exhi-vrshowroom_iframe-wrap{
    width: 100%;
    height: 70vh;
    position: relative;
    
    &.fullscreen{
      height: 100vh;
      width: 100vw;
      left: 50%;
      margin-left: -50vw;
    }
    > .vrshowroom_togglebtn{
      position: absolute;
      top: 20px;
      left: 20px;
      i{
        padding-right: 8px;
      }
      .compress{
        display: none;
      }
      &.active{
        left: 30px;        
        .expand{
          display: none;
        }
        .compress{
          display: inline;
        }
      }
    }
  }
  .exhi-vrshowroom_iframe{
    width: 100%;
    height: 100%;
    border: 0;
  }
  .exhi-vrshowroom_banner{
    margin: 0 auto;
    text-align: center;
    a{
      display: inline-block;
      position: relative;
      overflow: hidden;
      &:before{
        background: $font-color-dark2;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        content: '';
        opacity: 0;
        @include transition;
        z-index: 1;

      }
      > img{
        @include transition;
      }
      span{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        width: 138px;
        height: 138px;
        background: $link-color2;
        color: #fff;
        border-radius: 50%;
        text-align: center;
        // padding-top: 2%;
        z-index: 2;
        @include rwd(laptop-l){
          width: 138px * .9;
          height: 138px * .9;          
        }
        @include rwd(laptop-s){
          width: 138px * .8;
          height: 138px * .8;  
        }
        @include rwd(tablet-xl){
          width: 138px * .7;
          height: 138px * .7;  
        }
        @include rwd(mobile-xl){
          width: 138px * .6;
          height: 138px * .6;  
        }
        @include rwd(mobile-l){
          width: 138px * .5;
          height: 138px * .5;  
        }
        i{
          font-size: 74px;
          padding-left: 10%;
          padding-top: 22%;
          @include rwd(laptop-l){
            font-size: 74px * .9;
          }
          @include rwd(laptop-s){
            font-size: 74px * .8;
          }
          @include rwd(tablet-xl){
            font-size: 74px * .7;
          }
          @include rwd(mobile-xl){
            font-size: 74px * .6;
          }
          @include rwd(mobile-l){
            font-size: 74px * .5;
          }
        }
      }
      &:hover{
        &:before{
          opacity: .8;
        }
        > img{
          transform: scale(1.1);
        }
        span{
          background: #fff;
          i{
            color: $font-color-dark2;
          }
        }
      }
    }
  }
}
