//參觀者專區 - 填寫步驟1
.progress-step_wrap{
  margin: 40px 0 20px;
  @include rwd(mobile-l){
    margin: 25px 0 10px;
  }
}
.progress-step{
  @include clearul;
  display: flex;
  flex-wrap: wrap;
  counter-reset:stepnum;
  padding: 0 20%; //縮減步驟後，縮減寬度
  @include rwd(mobile-l){
    padding: 0;
  }

}
.progress-step_item{
  position: relative;  
  &:after{
    background-color: $font-color-light2;
  }
  &:not(:last-child){
    flex-basis: 1rem;
    flex-grow: 1;
    flex-shrink: 1;
    &:after{
      height: 1px;
      left: 60px;
      right: -60px;
      top: 20px;
      content: " ";
      display: block;
      position: absolute;
      @include rwd(mobile-l){
        left: 35px;
        right: -35px;
        top: 17px;
      }
    }
  }

  .progress-step_item_marker{
    align-items: center;
    // display: flex;
    border-radius: 50%;
    justify-content: center;
    position: relative;
    z-index: 1;
    width: 150px;
    line-height: 1.2em;
    @include rwd(mobile-xl){
      width: 100px;
    }
    @include rwd(mobile-l){
      // width: 70px;
    }
    text-align: center;
    &:before{
      background: $font-color-light2;
      width: 45px;
      height: 45px;
      counter-increment:stepnum; 
      content:counter(stepnum);
      display: block;
      font-size: 24px;
      font-weight: bold;
      color: $main-color;
      // text-align: center;
      border-radius: 50%;
      margin: 0 auto 10px;
      line-height: 1em;
      padding-top: 10px;
      @include rwd(laptop){
        font-size: 24px * .85;
        width: 45px * .85;
        height: 45px * .85;
      }
      @include rwd(mobile-xl){
        margin-bottom: 6px;
      }
      @include rwd(mobile-l){
        font-size: 24px * .7;
        width: 45px * .7;
        height: 45px * .7;
        padding-top: 8px;
      }
    }
    span{
      display: block;
      font-size: 18px;
      @include rwd(laptop){
        font-size: 17px;
      }
      @include rwd(tablet-l){
        font-size: 16px;
      }
      @include rwd(mobile-xl){
        font-size: 15px;
      }
      @include rwd(mobile-l){
        font-size: 14px;
      }
      @include rwd(mobile){
        font-size: 13px;
      }
    }
  }
  &.active{
    .progress-step_item_marker:before{
      background: $btn-default-hover-color;
      color: #fff;
    }
  }
}


