//數位展申請進度
.exhibitor-register_apply-process-wrap{
  border: 1px $font-color-light2 solid;
}
.apply-process_proportion{
  font-size: 45px;
  text-align: right;
  font-weight: 500;
  color: $link-color2;
  @include rwd(tablet-s){
    font-size: 45px * .8;
    line-height: 1.2em;
  }
  small{
    font-size: 24px;
  }
}
.apply-process_progress-bar{
  display: flex;
  flex-wrap: wrap;
  margin: 10px 0 25px;
}
.apply-process_progress-bar_item{
  width: 20%;
  display: flex; 
  text-align: center;
  position: relative;
  padding-top: 28px;
  &:before{
    top: 10px;
    left: 0;
    right: 0;
    height: 5px;
    background: $font-color-light2;
    content: '';
    display: block;
    position: absolute;
  }
  &:after{
    position: absolute;
    top: 12px;
    left: 50%;
    width: 14px;
    height: 14px;
    background: $font-color-light1;
    content: '';
    display: block;
    border-radius: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
  span{
    display: block;
    text-align: center;
    margin:0 auto;
    opacity: 0.5;
    word-break: break-all;
    line-height: 1.4;
    @include rwd(mobile-l){
      font-size: 13px;
    }
  }
  &.status_finished{
    &:before{
      background: $link-color2;
    }
    &:after{
      background: $link-color2;      
    }
    span{
      color: $link-color2;
      opacity: 1;
    }
  }
  &.status_active{
    &:before{
      background: $link-color2;
    }
    &:after{
      border: 6px $link-color2 solid;
      background: $font-color-light4;
      width: 24px;
      height: 24px;
    }
    span{
      color: $link-color2;
      opacity: 1;
    }
  }
}
.apply-process_stepinfo{
  ol{
    padding-left: 1.2em;
  }
  li{
    opacity: 0.5;
    &.status_finished{
      color: $link-color2;
      opacity: 1;
    }
    &.status_active{
      color: $link-color2;
      font-weight: bold;
      opacity: 1;
    }
  }
  li + li{
    margin-top: 0.3em;
    
  }
}


